@use "global" as *;
.article__btn {
  // size
  display: inline-block;
  // font
  font-family: "Noto Sans JP", sans-serif;
  font-size: rem(20);
  font-weight: $normal;
  letter-spacing: rem(2);
  line-height: 1.45;
  // decoration
  color: #333;
  border: 1px solid #333;
  background-color: #fff;
  overflow: hidden;
  // space
  padding: 0;
  // ajustment -> `.article__btn::after`
  position: relative;
  @include mq(md) {
    // font
    font-size: rem(16);
    letter-spacing: rem(0);
    line-height: 1.4375;
  }
  @include mq(xl) {
  }
}

.article__btn::after {
  content: "";
  // size
  display: block;
  width: 100%;
  height: 100%;
  background-color: #333;
  // ajustment -> `.article__btn`
  position: absolute;
  top: 0;
  left: -100%;
  // animation
  transition: 0.3s;
  @include mq(md) {
  }
  @include mq(xl) {
  }
}

.article__btn:hover::after {
  // ajustment
  left: 0;
  // animation
  transition: 0.3s;
  @include mq(md) {
  }
  @include mq(xl) {
  }
}

.article__btn:hover span {
  // font
  font-family: "Noto Sans JP", sans-serif;
  font-size: rem(20);
  font-weight: $normal500;
  // letter-spacing: rem(1);
  line-height: 1.45;
  // ajustment
  position: relative;
  z-index: 10;
  // decoration
  color: #fff;
  @include mq(md) {
    // font
    font-size: rem(16);
  }
  @include mq(xl) {
  }
}
